.user_list {
    height: 100%;
    flex: 4;
  }
  
  .user_list_user {
    display: flex;
    align-items: center;
  }
  
  .user_list_img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }
  
  .user_list_edit{
      border: none;
      border-radius: 10px;
      padding: 5px 10px;
      background-color: #3bb077;
      color: white;
      cursor: pointer;
      margin-right: 20px;
  }
  
  .user_list_delete{
      color: red;
      cursor: pointer;
  }